import React from 'react';
import PERSONAL_PROJECTS from './personal_projects';
import './personal_projects_list.css';

function PersonalProjectsList(props) {
    const items = PERSONAL_PROJECTS.map((project) => {
        return (
            <PersonalProjectListItem key={project.title} project={project} />
        );
    });
    return (
        <div className='personal-projects-list'>
            {items}
        </div>);
}

function PersonalProjectListItem(props) {
    const project = props.project;
    const skills = project.skills.join(", ");
    const linkTags = project.links.map((link) => {
        return (
            <a
                key={link.linkHref}
                className="project-link"
                href={link.linkHref}>
                <i className="material-icons">{link.icon}</i>
                    &nbsp;{link.linkText}
            </a>);
    });
    return (
        <div className="project">
            <div className="project-image-container">
                <img
                    className="project-image"
                    src={project.imgSrc} />
            </div>
            <div className="project-text">
                <div className="project-title">
                    {project.title}
                </div>
                <div className="project-description">
                    {project.description}
                </div>
                <div className="project-skills">
                    {skills}
                </div>
                <div className="project-links">
                    {linkTags}
                </div>
            </div>
        </div>);
}

export default PersonalProjectsList;