import React from 'react';
import './app.css';

import AboutMe from './about_me/about_me';
import PersonalProjectsList from './personal_projects/personal_projects_list';
import WorkExperiencesList from './work_experience/work_experiences_list';

const PROJECTS_TAB = 'projects';
const WORK_TAB = 'work';
const ABOUT_TAB = 'about';

/** Model used in the list view. */
class Entry {
    /** 
     * @param {string} title
     * @param {string} description
     * @param {string?} imgSrc
     * @param {!Array<string>} skills
     * @param {!Array<!Link>} links
     */
    constructor(title, description, imgSrc, skills, links) {
        this.title = title;
        this.description = description;
        this.imgSrc = imgSrc;
        this.skills = skills;
        this.links = links;
    }
}

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            tab: PROJECTS_TAB,
        };
    }

    render() {
        let body = '';
        if (this.state.tab === PROJECTS_TAB) {
            body = <PersonalProjectsList />;
        } else if (this.state.tab === WORK_TAB) {
            body = <WorkExperiencesList />;
        } else if (this.state.tab === ABOUT_TAB) {
            body = <AboutMe />;
        }
        return (
            <div className="app" >
                <link href="https://fonts.googleapis.com/icon?family=Material+Icons"
                    rel="stylesheet" />
                <Header setTab={(newTab) => { this.handleTabChange(newTab); }}
                    selectedTab={this.state.tab} />
                {body}
            </div>
        );
    }

    handleTabChange(newTab) {
        this.setState({
            tab: newTab,
        });
    }
}

function Header(props) {
    const getTabClass = (tab) => {
        let tabClass = 'app-link';
        if (props.selectedTab === tab) {
            return tabClass + ' app-link-selected';
        }
        return tabClass;
    }
    return (
        <div className="app">
            <div className="app-header">
                <div className="Jacob">
                    Jacob Stimes
                </div>
                <div className="app-link-container-container">
                    <div className="app-link-container">
                        <div
                            className={getTabClass(PROJECTS_TAB)}
                            onClick={() => { props.setTab(PROJECTS_TAB); }}>
                            Public  Projects
                        </div>
                        <div className="app-link-spacer">&nbsp;</div>
                        <div
                            className={getTabClass(WORK_TAB)}
                            onClick={() => { props.setTab(WORK_TAB); }}>
                            Work  History
                        </div>
                        <div className="app-link-spacer">&nbsp;</div>
                        <div
                            className={getTabClass(ABOUT_TAB)}
                            onClick={() => { props.setTab(ABOUT_TAB); }}>
                            About Me
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default App;
