import React from 'react';
import './work_experiences_list.css';
import WORK_EXPERIENCES from './work_experiences';

function WorkExperiencesList(props) {
    const items = WORK_EXPERIENCES.map((workExp) => {
        return (
            <WorkExperienceListItem key={workExp.company} workExperience={workExp} />
        );
    });
    return (
        <div className='work-experiences-list'>
            {items}
        </div>);
}

function WorkExperienceListItem(props) {
    const workExperience = props.workExperience;
    const titleText = `${workExperience.title} @ ${workExperience.company}`;
    const summaryText =
        `(${workExperience.dates}) -- ${workExperience.summary}`;
    const skills = workExperience.skills.join(", ");
    return (
        <div className="work-item">
            <div className="work-item-text">
                <div className="work-item-title">
                    {titleText}
                </div>
                <div className="work-item-description">
                    {summaryText}
                </div>
                <div className="work-item-skills">
                    {skills}
                </div>
            </div>
        </div>);
}

export default WorkExperiencesList;